<template>
	<div class="search">
		<div class="top">
			<input type="text" placeholder="搜索职位、公司或地点">
			<button class="btn">搜索</button>
		</div>
		<p>热门搜索：<a href="">更快入职</a>
			<a href="">简历代写</a>
			<a href="">热门模板</a>
		</p>
	</div>

	<div class="fixedLeft">
		<div class="fixedContent">
			<a class="iconfont icon-shoucang" href="" :class="{active:itemList.itemIndex == 1}" @mouseenter="get(1)"
				@mouseleave="move(0)">
				<span v-show="itemList.itemIndex === 1">收藏职位</span>
			</a>
			<a class="iconfont icon-lijitoudi" href="" :class="{active:itemList.itemIndex == 2}" @mouseenter="get(2)"
				@mouseleave="move(0)">
				<span v-show="itemList.itemIndex === 2">投递记录</span>
			</a>
			<a class="iconfont icon-dingyue" href="" :class="{active:itemList.itemIndex == 3}" @mouseenter="get(3)"
				@mouseleave="move(0)">
				<span v-show="itemList.itemIndex === 3">职位订阅</span>
			</a>
			<a class="iconfont icon-yaoqinghan" href="" :class="{active:itemList.itemIndex == 4}" @mouseenter="get(4)"
				@mouseleave="move(0)">
				<span v-show="itemList.itemIndex === 4">面试邀请</span>
			</a>
			<a class="iconfont icon-xiugai" href="" :class="{active:itemList.itemIndex == 5}" @mouseenter="get(5)"
				@mouseleave="move(0)">
				<span v-show="itemList.itemIndex === 5">简历精修</span>
			</a>
			<a class="iconfont icon-erweima" href="" :class="{active:itemList.itemIndex == 6}" @mouseenter="get(6)"
				@mouseleave="move(0)">
				<span v-show="itemList.itemIndex === 6">
					<p>扫码下载App</p>
				</span>
			</a>
		</div>
	</div>

	<!-- 回到顶部 -->
	<div class="toTop" v-show="itemList.showBtn">
		<span class="iconfont icon-huojian" @click="toTop"></span>
	</div>
</template>

<script setup>
	import {
		onMounted,
		reactive
	} from "vue";
	const itemList = reactive({
		itemIndex: 0,
		showBtn: false,
		scrollTop: 0,
		isTop: false
	})


	const showbtn = () => {
		let scrollTop =
			window.pageYOffset ||
			document.documentElement.scrollTop ||
			document.body.scrollTop;
		itemList.scrollTop = scrollTop;
		if (scrollTop > 1) {
			itemList.showBtn = true;
		} else {
			itemList.showBtn = false;
		}
	}

	onMounted(() => {
		window.addEventListener("scroll", showbtn, true);
		console.log(12)
	})
	const toTop = () => {
		var timer = setInterval(function() {
			let osTop =
				document.documentElement.scrollTop || document.body.scrollTop;
			let ispeed = Math.floor(-osTop / 5);
			document.documentElement.scrollTop = document.body.scrollTop = osTop + ispeed;
			itemList.isTop = true;
			if (osTop === 0) {
				clearInterval(timer);
			}
		}, 15);
	}
	let get = (index) => {
		itemList.itemIndex = index;
		console.log(itemList.itemIndex)
	}
	let move = (indexs) => {
		itemList.itemIndex = indexs;
		console.log(itemList.itemIndex)
	}
</script>

<style lang="scss">
	.search {
		background: #F2F5F4;
		padding: 1.3rem 20%;

		.top {
			display: flex;
			justify-content: start;
			align-items: center;
			margin: auto;

			input {
				width: 80%;
				font-size: 1rem;
				padding: 0.75rem 1rem;
				line-height: 1rem;
			}

			.btn {
				background: #00B38A;
				border: none;
				padding: 0.75rem 3rem;
				color: #ffffff;
				font-size: 1rem;
				cursor: pointer;
				text-align: center;
			}
		}

		p {
			margin: 0.5rem 0.5rem 0 0;
			text-align: left;
			font-size: 0.9rem;

			a {
				color: #0dca9f;
				margin-right: 0.5rem;
			}

			a:hover {
				text-decoration: underline;
			}
		}
	}

	.fixedLeft {
		position: fixed;
		top: 0;
		right: 0;
		z-index: 3;
		width: 3%;
		height: 100%;
		background: #fafafa;
		box-shadow: -4px 0 6px 0 rgba(0, 0, 0, 0.1);

		.fixedContent {
			display: flex;
			flex-direction: column;
			padding-top: 3rem;
			a {
				font-size: 1.5rem;
				display: block;
				margin: 0 auto 2.5rem;
				color: #0c0c0c;
				position: relative;

				span {
					position: absolute;
					top: 10%;
					right: 150%;
					color: #0c0c0c;
					font-size: 0.8rem;
					display: inline-block;
					width: 250%;
					height: 1.5rem;
					line-height: 1.5rem;
					background: #ffffff;
					text-align: center;
					box-shadow: -4px 0 6px 0 rgba(0, 0, 0, 0.2);
				}
			}

			a:nth-child(5) {
				color: #00B38A;
			}

			a:hover {
				color: #00B38A;
			}
		}
	}

	.toTop {
		position: fixed;
		bottom: 12%;
		right: 6%;

		span {
			font-size: 3rem;
			color: #848484;
		}
		span:hover{
			color: #ffaf0f;
		}
	}
</style>
