<template>
	<div class="content1">
		<div class="left">
			<ul class="meau">
				<li class="meauLi" v-for="(item,index) in data.meauData" :key="index">
					<h4>{{item.title}}</h4>
					<p class="leftp"><a href="">{{item.text1}}</a>
						<a href="">{{item.text2}}</a>
						<a href="">{{item.text3}}</a>
						<a href="">{{item.text4}}</a>
						<a href="">{{item.text5}}</a>
						<a href="">{{item.text6}}</a>
						<span :class="item.more"></span>
					</p>
					<ul class="submeau">
						<p class="title">{{item.title}}</p>
						<li v-for="(subItem,index) in item.submeanData" :key="index">
							<span>{{subItem.title}}</span>
							<p>
								<a href="" v-for="(ThirdItem,index) in subItem.thirdData" :key="index">{{ThirdItem.text}}</a>
							</p>
						</li>
					</ul>
				</li>
			</ul>
		</div>

		<div class="right" @mouseenter="MouseFun('移入')" @mouseleave="MouseFun('移出')">
			<ul class="imgShow" :style="{left:`-${data.leftVal}px`,transition:`${data.ition}s`}">
				<li v-for="(imgItem,index) in data.imgshowData" :key="index"
					:style="{ left:`${index*(data.imgWidth)}px`}">
					<img :src="imgItem.imgUrl" alt="">
				</li>
			</ul>

			<div class="leftControl" v-show="data.show1">
				<span class="iconfont icon-arrow-left" @click="PrevFun"></span>
			</div>

			<div class="rightControl" v-show="data.show1">
				<span class="iconfont icon-arrow-right" @click="NextFun"></span>
			</div>
			<!-- 点 -->
			<ul class="dotBox">
				<li v-for="(item,index) in data.imgshowData" :key="index" @click="instFun(index)">
					<span class="insTot" :class="{ color: index == data.imgIndex }">
					</span>
				</li>
			</ul>
			<!-- 点 end-->
		</div>
	</div>
</template>

<script setup>
	import {
		onMounted,
		reactive
	} from 'vue';
	import axios from 'axios'
	const data = reactive({
		meauData:[{}],
		imgshowData: [{}],
		leftVal: 0, // 轮播图盒子的偏移值
		ition: 0.8, //动画过渡时间
		imgWidth: 680, //图片宽度
		start: null,
		show1: false,
		imgIndex: 0
	})
	onMounted(() => {
		startInterval(); //自动执行定时器
	})
	// 数据模块
	axios.get('https://mock.apifox.cn/m1/2920208-0-default/bannerLeft', {
		
		})
		.then((result) => {
			data.meauData = result.data.meauData
			console.log("axios",result.data)
			}
		)
		// 图片轮播
		axios.get('https://mock.apifox.cn/m1/2920208-0-default/bannerImg', {
			
			})
			.then((result) => {
				data.imgshowData = result.data.imgshowData
				console.log("axios",result.data)
				}
			)
	const startInterval = () => {
		// 事件里定时器应该先清除在设置，防止多次点击直接生成多个定时器
		data.start = setInterval(() => {
			NextFun();
		}, 3000);
	};

	// 下一个
	const NextFun = () => {
		if (data.leftVal == (data.imgshowData.length - 1) * data.imgWidth) { //判断是否为最后一张
			data.ition = 0; // 将过渡时间变成0，瞬间位移到最后一张图
			data.leftVal = 0; // 瞬间移动到最后
			data.imgIndex = 0;
		} else {
			data.ition = 0.8; //必须要调用一次，否则不会出现动画
			data.leftVal += data.imgWidth;
			data.imgIndex = data.leftVal / data.imgWidth;
		}
	};

	// 上一个
	const PrevFun = () => {
		if (data.leftVal == 0) { //判断是否为第一张
			data.ition = 0; // 将过渡时间变成0，瞬间位移到最后一张图
			data.leftVal = (data.imgshowData.length - 1) * data.imgWidth; // 瞬间移动到最后
			data.imgIndex = data.imgshowData.length - 1;
		} else {
			data.ition = 0.8;
			data.leftVal -= data.imgWidth;
			data.imgIndex = data.leftVal / data.imgWidth;
		}
	};
	// 鼠标移入，轮播暂停
	const MouseFun = (type) => {
		type == '移入' ? clearTimeout(data.start) : startInterval(); //停、开始止自动轮播
		if (type == '移入') {
			data.show1 = true;
		} else {
			data.show1 = false;
		}
	};
	
	//点击原点
	const instFun = (index) => {
			data.ition = 0.8;
			data.leftVal = index * data.imgWidth;
			data.imgShow = index;
			data.imgIndex=index;
		}
</script>

