<script setup>
	import {
		onMounted,
		reactive
	} from 'vue';
	const data = reactive({
		maskShow: false,
		informShow:false
	})
// 点击显示和关闭遮罩层
	const setMaskShow = () => {
		data.maskShow = !data.maskShow;
		console.log(data.maskShow)
	}
// 阻止内容区域click的事件冒泡
	const handler = (event) => {
		// 在子类中调用stopPropagation()方法即可禁止父标签的点击事件的传递
		event.stopPropagation()
		// 阻止事件的传播
		event.cancelBubble = true
	}
	const setinformShow=()=>{
		data.informShow = !data.informShow;
	}
	onMounted(()=>{
		console.log(data.maskShow)
	})
